.footer {
    min-height: 30vh;
    display: flex;
    flex-direction: column;
    justify-items: center;
    align-items: center;
    background-color: #070d1b;
    color: white;
}

.footerImgContainer {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 350px;
    margin: 40px;
}

.footerImg img {
    width: 48px;
    height: 48px;
    border-radius: 50%;
}

.footerImg:hover {
    transform: scale(1.2, 1.2);
    transition: 300ms;
}

.footer h2 {
    font-size: 24px;
    font-weight: 500;
    text-transform: uppercase;
    color: #fec544;
    letter-spacing: 1px;
}